import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import { Link, useHistory } from 'react-router-dom'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../ClientSideDataTable'
import { formatReadableDate } from '../../../../../helper'

const ServiceservicePointRuleList = () => {
    const AssignedMenus = JSON.parse(localStorage.getItem('AssignedMenus')) || []
    const Array2D = AssignedMenus.map(x => x.submenu.map(y => y.id))
    const subMenuIDs = [].concat(...Array2D)

    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [resetData, setReset] = useState(true)
    const [servicePointRuleList, setservicePointRuleList] = useState([])

    const [modal, setModal] = useState(false)
    const toggleModal = () => setModal(!modal)

    useEffect(() => {
        localStorage.setItem('usePMStoken', true)
        useJwt.getServicePointRules().then(res => {
            console.log(res)
            setservicePointRuleList(res.data.data)
            localStorage.setItem('usePMStoken', false)
            setTableDataLoading(false)
        }).catch(err => {
            Error(err)
            console.log(err)
            setTableDataLoading(false)
            localStorage.setItem('usePMStoken', false)
        })
    }, [resetData])
    const handlePoPupActions = (rule_id, message) => {
        localStorage.setItem('usePMStoken', true)
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                return useJwt.deleteServicePointRule({ rule_id }).then(res => {
                    Success(res)
                    console.log(res)
                    setservicePointRuleList(servicePointRuleList.filter(x => x.id !== rule_id))
                    localStorage.setItem('usePMStoken', false)
                }).catch(err => {
                    localStorage.setItem('usePMStoken', false)
                    console.log(err.response)
                    Error(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {

            }
        })

    }
    const column = [
        {
            name: 'SL',
            minWidth: '20px',
            sortable: true,
            cell: (row, index) => index + 1  //RDT provides index by default
        },
        {
            name: 'User Type',
            minWidth: '80px',
            sortable: true,
            selector: 'user_type'
        },
        {
            name: 'Service Type',
            minWidth: '170px',
            sortable: true,
            selector: 'service_type'
        },
        {
            name: 'Amount',
            minWidth: '80px',
            sortable: true,
            selector: 'amount'
        },
        {
            name: 'Is Range',
            minWidth: '100px',
            sortable: true,
            selector: (item) => {
                return item.IsRange ? 'True' : 'False'
            }
        },
        {
            name: 'Start Range',
            minWidth: '100px',
            sortable: true,
            selector: 'start_range'
        },
        {
            name: 'End Range',
            minWidth: '100px',
            sortable: true,
            selector: 'end_range'
        },
        {
            name: 'Is Active',
            minWidth: '100px',
            sortable: true,
            selector: (item) => {
                return item.is_active ? 'True' : 'False'
            }
        },
        {
            name: 'Receiver Point',
            minWidth: '100px',
            sortable: true,
            selector: 'receiver_point'
        },
        {
            name: 'Sender Point',
            minWidth: '100px',
            sortable: true,
            selector: 'sender_point'
        },
        {
            name: 'Expiry Date',
            minWidth: '250px',
            sortable: true,
            selector: (item) => {
                return item.expiry_date ? formatReadableDate(item.expiry_date) : null
            }
        },
        {
            name: 'Created By',
            minWidth: '100px',
            sortable: true,
            selector: 'created_by'
        },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Delete">
                        <Trash size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => handlePoPupActions(row.id, 'Do you want to delete?')}
                        />
                    </span>
                </>
            }
        }
    ]
    return (
        <Card>
            <CardHeader className='border-bottom'>
                <CardTitle tag='h4'>Service Point Rules</CardTitle>
                {subMenuIDs.includes(18) && <Button.Ripple className='ml-2' color='primary' tag={Link} to='/createServicePointRule' >
                    <div className='d-flex align-items-center'>
                        <Plus size={17} style={{ marginRight: '5px' }} />
                        <span >Create Service Point Rule</span>
                    </div>
                </Button.Ripple>}
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md='12'>
                        <CommonDataTable column={column} TableData={servicePointRuleList} TableDataLoading={TableDataLoading} />
                    </Col>
                </Row>
            </CardBody>
        </Card>
    )
}

export default ServiceservicePointRuleList